body {
    font-family: "mosk-normal", sans-serif;
    transition: all .5s cubic-bezier(.645,.005,.02,1);
    color:#00393f;
}
.visible-small {
    display:none !important;
}

.hero {
    padding: 48px;
    background:url('../images/bg-hero.jpg') no-repeat top center;
    background-size:cover;
    min-height: 100vh;
    transition: all 0.8s linear;
    position:relative;
}
