.phone .hero {
    text-align: center;
    background:url('../images/bg-hero-mobile.jpg') no-repeat top center;
    background-size:cover;
    min-height:576px;
}
.phone .hero__slogan {
    display:none;
}
.descricao {
    background:url('../images/bg-descricao.jpg') no-repeat top center;
    color: #fff;
    padding-top:85px;
    font-family: "mosk-light", sans-serif;
    font-size:25px;
    letter-spacing:2px;
}
.descricao p {
    padding-right:80px;
}


.phone .descricao {
    /*text-shadow: 2px 2px rgba(255, 255, 255, 0.4);*/
    color:#00393f;
}
.phone .descricao p {
    text-align: center;
    padding: 10px;
    background:rgba(255, 255, 255, 0.75);
}
.phone .descricao img {
    width:75% !important;
    display:block;
    margin:0 auto 40px auto;
}
.full-height {
    min-height:100vh;
    background-size:cover;
}
.main-menu {
    padding:0;
    margin-top:30px;
    list-style: none;

}
.main-menu__item {
    margin-bottom:5px;
}
.main-menu__item a {
    color:#fff;
    font-family: "mosk-bold", sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size:23px;
    /*text-shadow: 2px 2px 5px rgba(35, 35, 35, 0.5), 0px 0px 5px #fff;*/
    text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}
.main-menu__item a:hover {
    text-decoration:none;
    /*color:#003732;*/
    color: #d3fffc;
}
.diferenciais {
    font-family: "mosk-light", sans-serif;
    padding-top:200px;
    padding-bottom:200px;
    font-size:23px;
    letter-spacing:2px;
}

.phone .diferenciais {
    padding-top:40px;
    padding-bottom:40px;
}
.phone .diferenciais img {
    width:75%;
    margin: 0 auto 40px auto;
}
.diferenciais strong {
    font-family: "mosk-bold", sans-serif;
    font-weight: bold;
}
.diferenciais ul {
    list-style: none;
    padding:0;
    margin:0;
}
.phone .diferenciais li {
    font-size:16px;
}
.diferenciais ul li:before {
    content:".";
    font-size:23px;
}
footer {
    margin-top: 70px;
}
.footer__divider {
    background:#003a3f;
    height:51px;
    font-size:27px;
    font-family: "mosk-normal", sans-serif;
    color:#fff;
    line-height: 51px;
    text-align: center;
    word-spacing:7px;
}
.footer__divider a {
    color:#fff;
}
.footer__divider a:hover {
    text-decoration:none;
}
.footer__divider a.facebook:hover {
    color:#8b9dc3;
}
.footer__divider a.instagram:hover {
    color:#f587d2;
}
.disclaimer {
    font-size:14px;
    text-align: justify;
    margin:50px 0 142px 0;
    font-family: "Arial Narrow", sans-serif;
    letter-spacing:1px;
}
.form-bar {
    width:100%;
    height:112px;
    background:url('../images/bg-barra.jpg') repeat-x center center;
    position:fixed;
    bottom:0;
    color:#fff;
    z-index: 2000;
}
.phone .form-bar {
    height:auto;
    background: #003732;
    text-align: center;
    padding: 20px 0;
}
.phone .form-bar form {
    display:none;
}
.phone .form-bar input {
    width:100% !important;
}
.phone .form-bar .callout {
    text-align: center;
    font-size: 15px;
}
.phone .form-bar .callout a {
    text-decoration: underline;
    color:#fff;
}

.phone .form-bar.open .callout {
    padding-bottom: 20px;
}
.phone .form-bar.open .callout a {
    text-decoration:none;
}
.form-bar .callout {
    text-transform: uppercase;
    font-size: 25px;
    font-family: "mosk-bold", sans-serif;
    line-height: 25px;
}
.form-bar input, .form-bar input:focus, .form-bar input:disabled {
    background:rgba(255, 255, 255, 0.2);
    border:solid 1px #fff;
    color:white; 
    font-family: "mosk-bold", sans-serif;
    border-radius:0;
}
.form-bar .form-control::-webkit-input-placeholder {color:#fff;font-family: "mosk-normal", sans-serif;}
.form-bar input::-moz-placeholder{color:#fff;font-family: "mosk-normal", sans-serif;}
.form-bar input:-ms-input-placeholder{color:#fff;font-family: "mosk-normal", sans-serif;}
.form-bar input:-moz-placeholder {color:#fff;font-family: "mosk-normal", sans-serif;}
.form-bar input::placeholder {color:#fff;font-family: "mosk-normal", sans-serif;}

.form-bar button[type=submit] {
    color:#fff;
    font-family: "mosk-normal", sans-serif;
    font-size:20px;
    background: #b5532b;
    border:none;
    outline:none;
    padding:.25rem 1rem;
    cursor:pointer;
}
.form-bar button[type=submit]:hover {
    background:#c7643c;
}
.form-bar button[type=submit]:active {
    background:#8b3a19;
}

.form-bar button[type=submit]:disabled {
    background: #a5a5a5;
    cursor:default;
}
.form-bar .invalid-feedback {
    position: relative;
    top: -32px;
    right: 7px;
    text-align: right;
    float: right;
    height:0;
    padding:0;
    margin:0;
    color:#fff;
}
.form-bar .form-bar__message {
    font-family: "mosk-bold";
    font-size:25px;
    display:none;
}
.carousel__container {
    position:relative;
}
#carousel-next {
    position:absolute;
    right:10%;
    top:50%;
    z-index: 100;
}
#carousel-prev {
    position:absolute;
    left:10%;
    top:50%;
    z-index: 100;
}
.slick-dots {
    bottom:50px;
}
.slick-dots li button:before {
    opacity: 1;
    color: white;
    font-size:20px;
}
.slick-dots li.slick-active button:before {
    opacity: 1;
    color: #96c11f;
}
.phone .slick-dots {
    display:none !important;
}

.phone #carousel-next img, .phone #carousel-prev img {
    width:30px;
}

.news {
    background:#d2d3d5;
    padding-top:200px;
    padding-bottom:200px;
}
.phone .news {
    padding-top:40px;
    padding-bottom:40px;
}

.localizacao {
    padding-top:50px;
    padding-bottom:50px;
}

.news h1, .news h2, .news a{
    font-family:"acherus", "mosk-bold", sans-serif;
}

.news h1 {
    font-size:48px;
}
.phone .news h1 {
    font-size:28px;
}

.news h2 {
    font-size:23px;
}
.news a {
    font-size:23px;
    color:#003a3f;
}
.news a:hover {
    color:#386165;
    text-decoration:none;
}

.social {
    font-size:27px;
    font-family: "mosk-normal", sans-serif;
    color:#003a3f;
    line-height: 51px;
    text-align: right;
    word-spacing:7px;
}
.social a {
    color:#003a3f;
}
.social a.facebook:hover {
    color:#3b5998;
}
.social a.instagram:hover {
    color:#f587d2;
}
.phone .ml-4 {
    margin-left: 0 !important;
}
.phone .news__item-small {
    margin-bottom: 40px;
}

.img-title {
    margin-bottom: 40px;
}
