.section__title {
    font-family: "acherus", 'mosk-bold', sans-serif;
    font-weight: bold;
    text-align:center;
    padding:20px;
    width:100%;
    border:solid 4px #00393f;
    margin-bottom:50px;
    font-size:2.5rem !important;
}

@media (min-width:960px) {
    form {
        display:block !important;
    }
}
@media (max-width:960px) {

    .hero {
        text-align: center;
        background:url('../images/bg-hero-mobile.jpg') no-repeat top center;
        background-size:cover;
        min-height:576px;
    }
    .hero__slogan {
        display:none;
    }
    .descricao {
        /*text-shadow: 2px 2px rgba(255, 255, 255, 0.4);*/
        color:#00393f;
    }
    .descricao p {
        text-align: center;
        padding: 10px;
        background:rgba(255, 255, 255, 0.75);
    }
    .descricao img {
        width:75% !important;
        display:block;
        margin:0 auto 40px auto;
    }
    .diferenciais {
        padding-top:40px;
        padding-bottom:40px;
    }
    .diferenciais img {
        width:75%;
        margin: 0 auto 40px auto;
    }
    .diferenciais li {
        font-size:16px;
    }
    .form-bar {
        height:auto;
        background: #003732;
        text-align: center;
        padding: 20px 0;
    }
    .form-bar form {
        display:none;
    }
    .form-bar input {
        width:100% !important;
    }
    .form-bar .callout {
        text-align: center;
        font-size: 15px;
    }
    .form-bar .callout a {
        text-decoration: underline;
        color:#fff;
    }

    .form-bar.open .callout {
        padding-bottom: 20px;
    }
    .form-bar.open .callout a {
        text-decoration:none;
    }
    .slick-dots {
        display:none !important;
    }

    #carousel-next img, #carousel-prev img {
        width:30px;
    }
    .news {
        padding-top:40px;
        padding-bottom:40px;
    }
    .news h1 {
        font-size:28px;
    }
    .hero__slogan {
        display:none;
    }

    .img-responsive {
        width:100% !important;
    }
    .visible-small {
        display:block !important;
    }
    .hidden-small {
        display:none !important;
    }
    .ml-4 {
        margin-left: 0 !important;
    }
    .news__item-small {
        margin-bottom: 40px;
    }

}